@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

.Card-Container {
    display: grid;

    grid-template-columns: 100%;
    grid-template-rows: 5rem 8rem 10rem;
    grid-template-areas: "title"
                        "description"
                        "action";

    max-width: 30rem;
    height: auto;

    transition: all ease 200ms;

    margin-bottom: 5rem;

     position: relative;
    border-radius: 0.5rem;
    transition: all;
    transition-timing-function: ease-in;
}

.Card-Container .card-header{
    display: grid;

    grid-template-columns: 100%;
    grid-template-rows: 5rem 3rem 10rem;
    grid-template-areas: "title"
                        "description"
                        "action";
}

.Card-Container.hide, .Card-Container.active {
    display: inherit;
}

.Card-Container:hover {
    border-color: #fff;


}

.Card-Container .description {
    margin-top: 20rem;
    grid-area: description;

    margin: auto;
}

.Card-Container.active .card-header, .Card-Container.hide .card-header {
    display: grid;

    grid-template-columns: 3rem 90%;
    grid-template-rows: 30% 10%;
    grid-template-areas: "dropdown title"
                         "dropdown description";
}

.Card-Container .title {
    padding-top: 1rem;

    font-size: 1.5rem;

    grid-area: title;

    margin-bottom: 10rem;
}



.Card-Container .dropdown-button {
    display: none;
    margin-left: 2rem;
}

.Card-Container .add-action-button {
    display: flex;

    grid-area: action;

    font-size: 16px;

    width: 5rem;
    height: 5rem;

    margin: auto;
    border-radius: 50%;

    background-color: #6649b8;
    

}

.Card-Container .add-action-button:hover {
    background-color: #3a276d;;
}

.add-action-button .icon {
    font-size: 3rem;

    display: flex;

    margin: auto;
}



.Card-Container .card-content {
    display: none;
}

.Card-Container .card-footer {
    display: none;
}


.Card-Container.active {
    max-width: 42rem;
    height: auto;

    transition: all ease 200ms;
}



.Card-Container.active.hide .card-content {
    display: none;
}

.Card-Container.active.hide .card-footer {
    display: none;
}



.Card-Container.active .add-action-button {
    display: none;
}

.Card-Container.active .dropdown-button {
    display: block;
}

.Card-Container.active .card-content {
    display: block;
}

.Card-Container.active .card-footer {
    display: block;
}

.dropdown-button {
    transform: rotate(0deg);

    transition: all ease 200ms;
}

.dropdown-button.open {
    transform: rotate(-90deg);
}